import { Button, Dashicon } from "@wordpress/components";
import { useEffect, useState } from "@wordpress/element";
import { isEmpty } from "lodash";

import Styler from "./Styler";
import Tooltips from "./Tooltips";
import Material from "./Material";
import CheckboxRadio from "./CheckboxRadio";
import Bootstrap from "./Bootstrap";
import FieldIcons from "./FieldIcons";
import CustomThemes from "./CustomThemes";
import AI from "./AI";
import Settings from "./Settings";
import "./index.scss";
import SaveChangesPopup from "../Helpers/SaveChangesPopup";
import StylerInlineStyles from "./Styler/Styles";

const Panels = (props) => {
  // const [panelTriggerFromPopup, setPanelTriggerFromPopup] = useState("");
  const [panelTriggerFromPopup, setPanelTriggerFromPopup] = useState("");

  const menubarButtons = [
    {
      label: "Styler",
      icon: "superhero-alt",
      id: "styler",
    },
    {
      label: "Tooltips",
      icon: "info",
      id: "tooltips",
    },
    {
      label: "Material Design",
      icon: "paperclip",
      id: "material",
    },
    {
      label: "Checkbox & Radio",
      icon: "yes-alt",
      id: "checkboxRadio",
    },
    {
      label: "Bootstrap",
      icon: "star-filled",
      id: "bootstrap",
    },
    {
      label: "Field Icons",
      icon: "share-alt",
      id: "fieldIcons",
    },
    {
      label: "Custom Themes",
      icon: "games",
      id: "customThemes",
    },
    {
      label: "AI",
      icon: "buddicons-replies",
      id: "ai",
    },
    {
      label: "Settings",
      icon: "admin-generic",
      id: "boosterSettings",
    },
  ];

  useEffect(() => {
    const handleBeforeUnload = (event) => {
      if (props.hasChangesMade === true) {
        // Cancel the event
        event.preventDefault();
        // Chrome requires returnValue to be set
        event.returnValue = "";

        // Display confirmation message
        const confirmationMessage = "Are you sure you want to leave?";
        event.returnValue = confirmationMessage;
        return confirmationMessage;
      }
    };

    // Add event listener when the component mounts
    window.addEventListener("beforeunload", handleBeforeUnload);

    return () => {
      window.removeEventListener("beforeunload", handleBeforeUnload);
    };
  }, [props.hasChangesMade]);

  const onPanelChange = (activePanelId) => {
    if (props.hasChangesMade) {
      setPanelTriggerFromPopup(activePanelId);
    } else {
      props.setActiveMenuBar(activePanelId);
      // clear out active section on panel change
      props.setActiveSectionId("");
    }
  };

  // This will reset the settings ensuring the settings are reloaded from ajax call and nothing gets rendered till they are rendered
  const resetPanelSettings = (resetPanelId) => {
    switch (resetPanelId) {
      case "tooltips":
        props.setTooltipsSettings({});
        break;
      case "material":
        props.setMaterialSettings({});
        break;
      case "checkboxRadio":
        props.setCheckboxRadioSettings({});
        break;
      case "bootstrap":
        props.setBootstrapSettings({});
        break;
      case "fieldIcons":
        props.setFieldIconsSettings({});
        break;
      case "customThemes":
        props.setCustomThemeSettings({});
        break;
      case "tooltips":
        props.setTooltipsSettings({});
        break;
    }
  };

  //   Menu Bar Structure
  const menubarBtnStructure = () => {
    const structure = menubarButtons.map((button, index) => {
      let activeClass = "";

      if (button.id === props.activeMenuBar) {
        activeClass = " active-panel";
      }

      let panelLabel = <p className="menubar-btn-txt">{button.label}</p>;
      let panelClasses = `menubar-btn ${activeClass}`;

      if (button.id === "boosterSettings") {
        return "";
      }

      return (
        <div className={panelClasses} onClick={() => onPanelChange(button.id)}>
          <Dashicon icon={button.icon} />
          {panelLabel}
        </div>
      );
    });

    return structure;
  };

  const settingBtn = () => {
    let panelClasses = `menubar-btn settingsBtn`;

    if ("boosterSettings" === props.activeMenuBar) {
      panelClasses += " active-panel";
    }

    return (
      <>
        <div className="stla-panel-btn-margin-auto"></div>
        <div
          className={panelClasses}
          onClick={() => onPanelChange("boosterSettings")}
        >
          <Dashicon icon="admin-generic" />
        </div>
      </>
    );
  };

  return (
    <>
      <style>
        {`
          body #gform_wrapper_${props.formId} .gform_body .gform_page{
            display: none !important;
          }
  
          body #gform_wrapper_${props.formId} .gform_body #gform_page_${props.formId}_${props.activePage}{
            display: block !important;
          }
          
        `}
      </style>
      <div className="stla-panels-menubar">
        {menubarBtnStructure()}
        {settingBtn()}
      </div>
      <StylerInlineStyles
        stylerSettings={props.stylerSettings}
        materialSettings={props.materialSettings}
        formId={props.formId}
      />
      {!isEmpty(props.bootstrapSettings) &&
        window.wp.hooks.applyFilters(
          "stla_booster_bootstrap_styling",
          "",
          props.formId,
          props.bootstrapSettings
        )}
      {!isEmpty(props.checkboxRadioSettings) &&
        window.wp.hooks.applyFilters(
          "stla_booster_checkbox_radio_styling",
          "",
          props.formId,
          props.checkboxRadioSettings
        )}
      {!isEmpty(props.customThemesSettings) &&
        window.wp.hooks.applyFilters(
          "stla_booster_custom_themes_styling",
          "",
          props.formId,
          props.customThemesSettings
        )}
      {!isEmpty(props.fieldIconsSettings) &&
        window.wp.hooks.applyFilters(
          "stla_booster_field_icons_styling",
          "",
          props.formId,
          props.fieldIconsSettings,
          props.fieldIcons,
          props.materialSettings
        )}
      {!isEmpty(props.materialSettings) &&
        window.wp.hooks.applyFilters(
          "stla_booster_material_styling",
          "",
          props.formId,
          props.materialSettings
        )}
      {!isEmpty(props.tooltipsSettings) &&
        window.wp.hooks.applyFilters(
          "stla_booster_tooltip_styling",
          "",
          props.formId,
          props.tooltipsSettings
        )}

      <div className="stla-panels">
        {panelTriggerFromPopup !== "" && (
          <SaveChangesPopup
            hasChangesMade={props.hasChangesMade}
            activeMenuBar={props.activeMenuBar}
            panelTriggerFromPopup={panelTriggerFromPopup}
            setHasChangesMade={props.setHasChangesMade}
            setPanelTriggerFromPopup={setPanelTriggerFromPopup}
            setActiveMenuBar={props.setActiveMenuBar}
            resetPanelSettings={resetPanelSettings}
          />
        )}

        {props.activeMenuBar === "styler" && (
          <Styler
            stylerSettings={props.stylerSettings}
            setStylerSettings={props.setStylerSettings}
            generalSettings={props.generalSettings}
            setGeneralSettings={props.setGeneralSettings}
            formId={props.formId}
            activeMenuBar={props.activeMenuBar}
            fetchStylerSettings={props.fetchStylerSettings}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
            activeSectionId={props.activeSectionId}
            setActiveSectionId={props.setActiveSectionId}
          />
        )}

        {props.activeMenuBar === "tooltips" && (
          <Tooltips
            tooltipsSettings={props.tooltipsSettings}
            setTooltipsSettings={props.setTooltipsSettings}
            formId={props.formId}
            activeMenuBar={props.activeMenuBar}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
          />
        )}

        {props.activeMenuBar === "material" && (
          <Material
            materialSettings={props.materialSettings}
            setMaterialSettings={props.setMaterialSettings}
            formId={props.formId}
            activeMenuBar={props.activeMenuBar}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
          />
        )}

        {props.activeMenuBar === "checkboxRadio" && (
          <CheckboxRadio
            checkboxRadioSettings={props.checkboxRadioSettings}
            setCheckboxRadioSettings={props.setCheckboxRadioSettings}
            formId={props.formId}
            activeMenuBar={props.activeMenuBar}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
          />
        )}
        {props.activeMenuBar === "bootstrap" && (
          <Bootstrap
            bootstrapSettings={props.bootstrapSettings}
            setBootstrapSettings={props.setBootstrapSettings}
            formId={props.formId}
            activeMenuBar={props.activeMenuBar}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
          />
        )}
        {props.activeMenuBar === "fieldIcons" && (
          <FieldIcons
            fieldIcons={props.fieldIcons}
            setFieldIcons={props.setFieldIcons}
            fieldIconsSettings={props.fieldIconsSettings}
            setFieldIconsSettings={props.setFieldIconsSettings}
            formId={props.formId}
            activeMenuBar={props.activeMenuBar}
            formFieldsLabels={props.formFieldsLabels}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
          />
        )}
        {props.activeMenuBar === "customThemes" && (
          <CustomThemes
            stylerSettings={props.stylerSettings}
            setStylerSettings={props.setStylerSettings}
            customThemesSettings={props.customThemesSettings}
            activeMenuBar={props.activeMenuBar}
            setCustomThemesSettings={props.setCustomThemesSettings}
            formId={props.formId}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            allSettings={props.allSettings}
          />
        )}

        {props.activeMenuBar === "ai" && (
          <AI
            activeMenuBar={props.activeMenuBar}
            formId={props.formId}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            formFieldsLabels={props.formFieldsLabels}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            setFormFieldsLabels={props.setFormFieldsLabels}
            allSettings={props.allSettings}
          />
        )}

        {props.activeMenuBar === "boosterSettings" && (
          <Settings
            activeMenuBar={props.activeMenuBar}
            formId={props.formId}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
            formFieldsLabels={props.formFieldsLabels}
            totalPageCount={props.totalPageCount}
            activePage={props.activePage}
            setActivePage={props.setActivePage}
            setFormFieldsLabels={props.setFormFieldsLabels}
            boosterSettings={props.boosterSettings}
            setBoosterSettings={props.setBoosterSettings}
            activeSectionId={props.activeSectionId}
            setActiveSectionId={props.setActiveSectionId}
          />
        )}
      </div>
    </>
  );
};

export default Panels;
