import { applyFilters } from "../../../Helpers/createHooks";

const Sidebar = (props) => {
  const sidebarFields = () => {
    
    let sidebarFields = "";
    sidebarFields = window.wp.hooks.applyFilters(
      "stla_booster_ai_sidebar",
      sidebarFields,
      props.aiSettings,
      props.setAiSettings,
      props.formId,
      props.setHasChangesMade,
      props.activeFieldId,
      props.setActiveFieldId,
      props.formFieldsLabels,
      props.setFormFieldsLabels
    );

    // console.log(props.checkboxRadioSettings);
    return sidebarFields;
  };

  return <div className="stla-panel-sidebar">{sidebarFields()}</div>;
};

export default Sidebar;
