import { useState, useEffect } from "@wordpress/element";
import DOMPurify from "dompurify";
import parse from "html-react-parser";
import {
  Dashicon,
  DropdownMenu,
  Flex,
  FlexItem,
  Spinner,
} from "@wordpress/components";
import { edit } from "@wordpress/icons";

const ContentArea = (props) => {
  const [html, setHtml] = useState(false);
  const [confirmationHtml, setConfirmationHtml] = useState(false);
  // const [activeDevice, setActiveDevice] = useState("desktop");
  const [callingApi, setCallingApi] = useState(false);

  // fetches the form html very first time as well as on change of formid
  useEffect(() => {
    if (!props.formId) {
      setHtml(<div>Please choose the form in order to apply the styles.</div>);
      return;
    }

    (async () => {
      setCallingApi(true);
      const formData = new FormData();
      const allSettings = JSON.stringify(props.allSettings);

      formData.append("action", "stla_gravity_form_html");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", props.formId);
      formData.append("stla_all_settings", allSettings);

      try {
        await fetch(window.ajaxurl, {
          method: "POST",
          body: formData,
        })
          .then((response) => {
            if (!response.ok) {
              throw new Error("Network response was not ok");
            }
            return response.text(); // Parse response as text
          })
          .then((formHtml) => {
            const clean = DOMPurify.sanitize(formHtml, {
              USE_PROFILES: { html: true },
            });
            const parsedHtml = parse(clean);

            // Do something with the form HTML
            setHtml(parsedHtml);
            setTimeout(function () {
              document.dispatchEvent(new Event("gform_post_render"));
            }, 100);
            // props.setActivePage(1);
            // setActiveDevice("desktop");
          });
      } catch (error) {
        console.error("Error fetching form HTML:", error);
      }
      setCallingApi(false);
    })();
  }, [props.formId]);

  // confirmation page
  useEffect(() => {
    if (props.activePage !== "confirmation") {
      return;
    }

    (async () => {
      setCallingApi(true);
      const formData = new FormData();

      formData.append("action", "stla_gravity_form_confirmation_html");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", props.formId);

      try {
        await fetch(window.ajaxurl, {
          method: "POST",
          body: formData,
        })
          .then((response) => {
            if (!response.ok) {
              throw new Error("Network response was not ok");
            }
            return response.text(); // Parse response as text
          })
          .then((confirmationHtml) => {
            const clean = DOMPurify.sanitize(confirmationHtml, {
              USE_PROFILES: { html: true },
            });

            const parsedHtml = parse(clean);

            // Do something with the form HTML
            setConfirmationHtml(parsedHtml);
          });
      } catch (error) {
        console.error("Error fetching form HTML:", error);
      }
      setCallingApi(false);
    })();
  }, [props.activePage]);

  // Iterate through parsed elements to find the li element containing "this is mango"
  const attachEventHandlers = (element) => {
    // Check if the element is a valid React element
    if (!React.isValidElement(element)) {
      return element;
    }

    let allowedInput = ["text", "email", "password", "tel", "url", "number"];
    // on input Click
    if (element.props.type && allowedInput.indexOf(element.props.type) >= 0) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("textFields"),
      });
    }

    // on label Click
    if (
      element.props.className &&
      element.props.className.includes("gfield_label")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("fieldLabels"),
      });
    }

    // on button Click
    if (
      element.props.className &&
      (element.props.className.includes("gform_button") ||
        element.props.className.includes("gform_next_button") ||
        element.props.className.includes("gform_previous_button"))
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("submitButton"),
      });
    }

    // on List Click
    if (
      element.props.className &&
      (element.props.className.includes("gfield_list") ||
        element.props.className.includes("gfield_list_group"))
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("listField"),
      });
    }

    // on Radio Click
    if (
      element.props.className &&
      element.props.className.includes("ginput_container_radio")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("radioInputs"),
      });
    }

    // on Checkbox Click
    if (
      element.props.className &&
      element.props.className.includes("ginput_container_checkbox")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("checkboxInputs"),
      });
    }

    // on field description Click
    if (
      element.props.className &&
      element.props.className.includes("gfield_description")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("fieldDescriptions"),
      });
    }

    // on field Sublabel Click
    if (
      element.props.className &&
      element.props.className.includes(
        "gform-field-label gform-field-label--type-sub"
      )
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("subLabels"),
      });
    }

    // on Error Message Click
    if (
      element.props.className &&
      element.props.className.includes("gform_validation_errors")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("errorMessage"),
      });
    }

    // on dropdown
    if (
      element.props.className &&
      element.props.className.includes("gfield_select")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("dropdownFields"),
      });
    }

    // on section click
    if (
      element.props.className &&
      element.props.className.includes("gsection")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("sectionBreakTitleDescription"),
      });
    }

    // on textarea click
    if (
      element.props.className &&
      element.props.className.includes("ginput_container_textarea")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("paragraphTextareaFields"),
      });
    }

    // on form header click
    if (
      element.props.className &&
      element.props.className.includes("gform_heading")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("formHeader"),
      });
    }

    // on form title click
    if (
      element.props.className &&
      element.props.className.includes("gform_title")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("formTitleDescription"),
      });
    }

    // on form description click
    if (
      element.props.className &&
      element.props.className.includes("gform_description")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("formTitleDescription"),
      });
    }

    // on confirmation click
    if (
      element.props.className &&
      element.props.className.includes("gform_confirmation_message")
    ) {
      return React.cloneElement(element, {
        onClick: () => props.setActiveSectionId("confirmationMessage"),
      });
    }

    // If the element has children, recursively apply the function to each child
    if (element.props.children) {
      return React.cloneElement(element, {
        children: React.Children.map(
          element.props.children,
          attachEventHandlers
        ),
      });
    }

    return element;
  };

  // Apply event handlers to parsed elements
  const parsedHtmlWithEvents = !html
    ? ""
    : React.Children.map(html, attachEventHandlers);

  // Apply event handlers to parsed elements
  const parsedConfirmationHtmlWithEvents = !confirmationHtml
    ? ""
    : React.Children.map(confirmationHtml, attachEventHandlers);

  const responsiveToolbar = () => {
    let pageCountDropdownControls = [];

    for (let p = 0; p <= props.totalPageCount; p++) {
      pageCountDropdownControls.push({
        title: `Page: ${p + 1}`,
        onClick: () => props.setActivePage(p + 1),
      });
    }

    // confirmation
    pageCountDropdownControls.push({
      title: `Confirmation Page`,
      onClick: () => props.setActivePage("confirmation"),
    });

    return (
      <div className="stla-responsive-toolbar">
        <Flex justify="end">
          <FlexItem>
            <div className="stla-page-select">
              Selected Page:{" "}
              <span className="stla-selected-page">
                <strong>{props.activePage}</strong>
              </span>
              <DropdownMenu
                className="stla-page-select-dropdown"
                icon={edit}
                label="Change Selected Page"
                popoverProps={{
                  focusOnMount: true,
                }}
                controls={pageCountDropdownControls}
              />
            </div>
          </FlexItem>
        </Flex>
      </div>
    );
  };

  return (
    <>
      <div className="stla-panel-contentarea stla-responsive-area">
        {responsiveToolbar()}

        <div
          className="stla-form-wrapper stla-styler-form-wrapper stla-fields-has-hover"
          // style={{ width: activeDeviceWidth }}
          onClick={(e) => {
            e.preventDefault();
          }}
        >
          {callingApi && (
            <div className="stla-content-spinner">
              <Spinner />
            </div>
          )}

          {callingApi === false && parsedHtmlWithEvents}

          {props.activePage === "confirmation" &&
            callingApi === false &&
            parsedConfirmationHtmlWithEvents}
        </div>
      </div>
      ;
    </>
  );
};

export default ContentArea;
