import { addCssUnit } from "./index";

const cssGenerator = (stylerSettings) => {
  let inputStyles = "";

  if (stylerSettings["font-style"]) {
    inputStyles += "font-weight: normal !important; ";
  }
  if (
    stylerSettings["font-style"] !== undefined &&
    stylerSettings["font-style"] !== ""
  ) {
    const fontStyles = stylerSettings["font-style"].split("|");

    for (const value of fontStyles) {
      switch (value) {
        case "bold":
          inputStyles += "font-weight: bold !important; ";
          break;
        case "italic":
          inputStyles += "font-style: italic !important; ";
          break;
        case "uppercase":
          inputStyles += "text-transform: uppercase !important; ";
          break;
        case "underline":
          inputStyles += "text-decoration: underline !important; ";
          break;
        default:
          break;
      }
    }
  }
  inputStyles += isCssNotSet(stylerSettings, "color")
    ? ""
    : "color:" + stylerSettings["color"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "background-color")
    ? ""
    : "background-color:" + stylerSettings["background-color"] + " !important;";

  // Gradient for themes

  inputStyles += isCssNotSet(stylerSettings, "background-color1")
    ? ""
    : "background:-webkit-linear-gradient(to left," +
      stylerSettings["background-color"] +
      "," +
      stylerSettings["background-color1"] +
      ") !important;";

  inputStyles += isCssNotSet(stylerSettings, "background-color1")
    ? ""
    : "background:linear-gradient(to left," +
      stylerSettings["background-color"] +
      "," +
      stylerSettings["background-color1"] +
      ") !important;";

  inputStyles += isCssNotSet(stylerSettings, "gradient-color")
    ? ""
    : `background: ${stylerSettings["gradient-color"]};`;

  // inputStyles+= isCssNotSet(stylerSettings['padding'])?'':'padding:'+ stylerSettings['padding']+';';
  inputStyles += isCssNotSet(stylerSettings, "width")
    ? ""
    : "width:" + addCssUnit(stylerSettings["width"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "height")
    ? ""
    : "height:" + addCssUnit(stylerSettings["height"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "title-position")
    ? ""
    : "text-align:" + stylerSettings["title-position"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "text-align")
    ? ""
    : "text-align:" + stylerSettings["text-align"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "line-height")
    ? ""
    : "line-height:" + stylerSettings["line-height"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "error-position")
    ? ""
    : "text-align:" + stylerSettings["error-position"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "description-position")
    ? ""
    : "text-align:" + stylerSettings["description-position"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "title-color")
    ? ""
    : "color:" + stylerSettings["title-color"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "font-color")
    ? ""
    : "color:" + stylerSettings["font-color"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "description-color")
    ? ""
    : "color:" + stylerSettings["description-color"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "button-color")
    ? ""
    : "background-color:" + stylerSettings["button-color"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "description-color")
    ? ""
    : "color:" + stylerSettings["description-color"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "font-family")
    ? ""
    : "font-family:" + stylerSettings["font-family"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "font-size")
    ? ""
    : "font-size:" + addCssUnit(stylerSettings["font-size"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "max-width")
    ? ""
    : "width:" + addCssUnit(stylerSettings["max-width"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "maximum-width")
    ? ""
    : "width:" + addCssUnit(stylerSettings["maximum-width"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "margin")
    ? ""
    : "margin:" + addCssUnit(stylerSettings["margin"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "padding")
    ? ""
    : "padding:" + addCssUnit(stylerSettings["padding"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "border-size")
    ? ""
    : "border-width:" +
      addCssUnit(stylerSettings["border-size"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "border-color")
    ? ""
    : "border-color:" + stylerSettings["border-color"] + " !important;";
  if (stylerSettings["border-size"] !== undefined) {
    inputStyles += isCssNotSet(stylerSettings, "border-type")
      ? "border-style:solid !important;"
      : "border-style:" + stylerSettings["border-type"] + " !important;";
  }
  // inputStyles += isCssNotSet(stylerSettings, 'border-type') ? '' : 'border-style:' + stylerSettings['border-type'] + ' !important;';
  inputStyles += isCssNotSet(stylerSettings, "border-bottom")
    ? ""
    : "border-bottom-style:" + stylerSettings["border-bottom"] + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "border-bottom-size")
    ? ""
    : "border-bottom-width:" +
      addCssUnit(stylerSettings["border-bottom-size"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "border-bottom-color")
    ? ""
    : "border-bottom-color:" +
      stylerSettings["border-bottom-color"] +
      " !important;";

  inputStyles += isCssNotSet(stylerSettings, "background-image")
    ? ""
    : "background: url(" +
      stylerSettings["background-image"] +
      ") no-repeat !important;";

  inputStyles += isCssNotSet(stylerSettings, "border-bottom-color")
    ? ""
    : "border-bottom-color:" +
      stylerSettings["border-bottom-color"] +
      " !important;";
  if (stylerSettings["display"] !== undefined) {
    inputStyles += stylerSettings["display"] ? "display:none !important;" : "";
  }

  if (stylerSettings["visibility"] !== undefined) {
    inputStyles += stylerSettings["visibility"]
      ? "visibility: hidden !important;"
      : "";
  }

  if (stylerSettings["border-radius"] !== undefined) {
    inputStyles +=
      "border-radius:" +
      addCssUnit(stylerSettings["border-radius"]) +
      " !important;";
    inputStyles +=
      "-web-border-radius:" +
      addCssUnit(stylerSettings["border-radius"]) +
      " !important;";
    inputStyles +=
      "-moz-border-radius:" +
      addCssUnit(stylerSettings["border-radius"]) +
      " !important;";
  }
  inputStyles += isCssNotSet(stylerSettings, "custom-css")
    ? ""
    : stylerSettings["custom-css"] + " !important;";

  inputStyles += isCssNotSet(stylerSettings, "padding-left")
    ? ""
    : "padding-left:" +
      addCssUnit(stylerSettings["padding-left"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "padding-right")
    ? ""
    : "padding-right:" +
      addCssUnit(stylerSettings["padding-right"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "padding-top")
    ? ""
    : "padding-top:" +
      addCssUnit(stylerSettings["padding-top"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "padding-bottom")
    ? ""
    : "padding-bottom:" +
      addCssUnit(stylerSettings["padding-bottom"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "margin-left")
    ? ""
    : "margin-left:" +
      addCssUnit(stylerSettings["margin-left"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "margin-right")
    ? ""
    : "margin-right:" +
      addCssUnit(stylerSettings["margin-right"]) +
      " !important;";
  inputStyles += isCssNotSet(stylerSettings, "margin-top")
    ? ""
    : "margin-top:" + addCssUnit(stylerSettings["margin-top"]) + " !important;";
  inputStyles += isCssNotSet(stylerSettings, "margin-bottom")
    ? ""
    : "margin-bottom:" +
      addCssUnit(stylerSettings["margin-bottom"]) +
      " !important;";

  return inputStyles;
};

const isCssNotSet = (setting, property) => {
  if (setting[property] !== undefined && setting[property] !== "") {
    return false;
  }
  return true;
};

export default cssGenerator;
