import Components from "../../../../Helpers/Components";

const SubmitButton = (props) => {
  const componentData = [
    {
      type: "alignment",
      id: "button-align",
      label: "Alignment",
    },
    { type: "color", id: "button-color", label: "Background Color" },
    { type: "color", id: "hover-color", label: "Hover Background Color" },
    { type: "fontStyle", id: "font-style", label: "Font Style" },
    {
      type: "responsive",
      id: ["font-size", "font-size-tab", "font-size-phone"],
      label: "Font Size",
    },
    {
      type: "responsive",
      id: ["line-height", "line-height-tab", "line-height-phone"],
      label: "Line Height",
    },

    { type: "color", id: "font-color", label: "Font Color" },
    { type: "color", id: "font-hover-color", label: "Hover Font Color" },
    {
      type: "responsive",
      id: ["max-width", "max-width-tab", "max-width-phone"],
      label: "Width",
    },

    {
      type: "responsive",
      id: ["height", "height-tab", "height-phone"],
      label: "Height",
    },
    {
      type: "border",
      id: ["border-size", "border-type", "border-color"],
      label: "Border",
    },
    { type: "unit", id: "border-radius", label: "Border Radius" },

    { type: "spacing", id: "margin", label: "Margin" },
    { type: "spacing", id: "padding", label: "Padding" },
  ];

  return (
    <div className="stla-sidebar-settings">
      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="submit-button"
        settings={componentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
    </div>
  );
};
export default SubmitButton;
