import { applyFilters } from "../../../Helpers/createHooks";

const Sidebar = (props) => {
  
  const sidebarFields = () => {
    let sidebarFields = window.wp.hooks.applyFilters(
      "stla_booster_material_sidebar",
      "",
      props.materialSettings,
      props.setMaterialSettings,
      props.formId,
      props.setHasChangesMade
    );
    return sidebarFields;
  };

  return <div className="stla-panel-sidebar">{sidebarFields()}</div>;
};

export default Sidebar;
