import { Dashicon } from "@wordpress/components";

const SidebarHeader = (props) => {
  let activeLabel = "";

  props.sections.forEach((section) => {
    if (section.id === props.activeSectionId) {
      activeLabel = section.label;
    }
  });

  return (
    <div className="stla-sidebar-header">
      <div
        className="stla-sidebar-back-btn"
        onClick={() => props.setActiveSectionId("")}
      >
        <Dashicon icon="arrow-left-alt2" />
      </div>
      <h3 className="stla-sidebar-heading">{activeLabel}</h3>
    </div>
  );
};

export default SidebarHeader;
