import { Modal, Dashicon, Button, Flex, FlexItem } from "@wordpress/components";
import { cloneDeep } from "lodash";

const SaveChangesPopup = (props) => {
  const closeModal = () => {
    props.hasChangesMade(false);
  };

  const switchTab = () => {
    const newTriggeredPopup = cloneDeep(props.panelTriggerFromPopup);
    props.resetPanelSettings(props.activeMenuBar);
    props.setHasChangesMade(false);
    props.setActiveMenuBar(newTriggeredPopup);
    props.setPanelTriggerFromPopup("");
  };

  if (props.hasChangesMade) {
    return (
      <>
        {}
        <Modal
          title="You have unsaved changes"
          onRequestClose={closeModal}
          className="stla-save-modal-container"
          __experimentalHideHeader={true}
          style={{ width: "600px" }}
          isDismissible={false}
          shouldCloseOnClickOutside={false}
          shouldCloseOnEsc={false}
          overlayClassName="stla-modal-overlay"
        >
          <div className="stla-pro-modal">
            <div className="stla-modal-icon">
              <Dashicon icon="info" />
            </div>

            <div className="stla-modal-content">
              <h3>Alert</h3>
              <p>
                You have unsaved changes. If you switch before saving then the
                changes will be lost.
              </p>
              <Flex justify="center">
                <FlexItem>
                  <Button variant="primary" onClick={switchTab}>
                    Switch Anyway
                  </Button>
                </FlexItem>
                <FlexItem>
                  <Button
                    variant="secondary"
                    onClick={() => props.setPanelTriggerFromPopup("")}
                  >
                    Cancel
                  </Button>
                </FlexItem>
              </Flex>
            </div>

            {/* <div className="stla-modal-footer">
                <div className="stla-modal-notice">{props.content.notice}</div>
              </div> */}
          </div>
        </Modal>
      </>
    );
  }
  return "";
};

export default SaveChangesPopup;
