import { __experimentalBorderControl as BorderControl } from "@wordpress/components";
import { cloneDeep } from "lodash";

const Border = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  let disableCustomColors = false;
  if (props.settingId.indexOf("border-color") < 0) {
    disableCustomColors = true;
  }

  const updateBorder = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    // console.log(value);

    if (
      value.color !== undefined &&
      props.settingId.indexOf("border-color") >= 0
    ) {
      allStylerSettings[props.sectionId]["border-color"] = value.color;
    }

    if (value.width !== undefined) {
      allStylerSettings[props.sectionId]["border-size"] = value.width;
    }
    if (value.style !== undefined) {
      allStylerSettings[props.sectionId]["border-type"] = value.style;
    }
    props.setHasChangesMade(true);
    props.setPanelData(allStylerSettings);
  };

  // get properties value from object.
  let borderColor = "";
  if ("border-color" in sectionSettings) {
    borderColor = sectionSettings["border-color"];
  }

  let borderStyle = "";
  if ("border-type" in sectionSettings) {
    borderStyle = sectionSettings["border-type"];
  }

  let borderWidth = "";
  if ("border-size" in sectionSettings) {
    borderWidth = sectionSettings["border-size"];
  }

  return (
    <BorderControl
      // colors={colors}
      label={props.label}
      value={{
        color: borderColor,
        style: borderStyle,
        width: borderWidth,
      }}
      disableCustomColors={disableCustomColors}
      enableAlpha={false}
      onChange={updateBorder}
    />
  );
};

export default Border;
