import Components from "../../../../Helpers/Components";

const FormTitleDescription = (props) => {
  const titleComponentData = [
    { type: "color", id: "font-color", label: "Title Font Color" },
    { type: "fontStyle", id: "font-style", label: "Title Font Style" },
    {
      type: "alignment",
      id: "text-align",
      label: "Title Font Alignment",
    },
    {
      type: "responsive",
      id: ["font-size", "font-size-tab", "font-size-phone"],
      label: "Title Font Size",
    },
    {
      type: "responsive",
      id: ["line-height", "line-height-tab", "line-height-phone"],
      label: "Title Line Height",
    },
    { type: "spacing", id: "padding", label: "Title Padding" },
    { type: "spacing", id: "margin", label: "Title Margin" },
  ];
  const descriptionComponentData = [
    { type: "color", id: "font-color", label: "Description Font Color" },
    { type: "fontStyle", id: "font-style", label: "Description Font Style" },
    {
      type: "alignment",
      id: "text-align",
      label: "Description Font Alignment",
    },
    {
      type: "responsive",
      id: ["font-size", "font-size-tab", "font-size-phone"],
      label: "Description Font Size",
    },
    {
      type: "responsive",
      id: ["line-height", "line-height-tab", "line-height-phone"],
      label: "Description Line Height",
    },
    { type: "spacing", id: "padding", label: "Description Padding" },
    { type: "spacing", id: "margin", label: "Description Margin" },
  ];

  return (
    <div className="stla-sidebar-settings">
      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="form-title"
        settings={titleComponentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="form-description"
        settings={descriptionComponentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
    </div>
  );
};
export default FormTitleDescription;
