import {
  Flex,
  FlexBlock,
  Dashicon,
  __experimentalUnitControl as UnitControl,
} from "@wordpress/components";
import { __experimentalUseFixedWindowList } from "@wordpress/compose";
import { cloneDeep } from "lodash";

const Responsive = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  const updateResponsiveValue = (key, value) => {
    let modifyPanelData = cloneDeep(props.panelData);
    modifyPanelData[props.sectionId][key] = value;
    props.setHasChangesMade(true);
    props.setPanelData(modifyPanelData);
  };

  //  field HTML Strucutre.
  const responsiveStructure = () => {
    const responsiveSettings = cloneDeep(props.settingId);

    const settingFields = responsiveSettings.map((setting) => {
      let settingIcon = "";

      if (setting.indexOf("tab") >= 0) {
        settingIcon = "tablet";
      } else if (setting.indexOf("phone") >= 0) {
        settingIcon = "smartphone";
      } else {
        settingIcon = "desktop";
      }

      // console.log(setting.indexOf("tab"));

      return (
        <FlexBlock>
          <Dashicon icon={settingIcon} />
          <UnitControl
            onChange={(value) => updateResponsiveValue(setting, value)}
            value={sectionSettings[setting]}
          />
        </FlexBlock>
      );
    });

    return (
      <>
        <label className="stla-sidebar-custom-label">{props.label}</label>
        <Flex>{settingFields}</Flex>
      </>
    );
  };

  return <>{responsiveStructure()}</>;
};

export default Responsive;
