import Components from "../../../../Helpers/Components";
const ListField = (props) => {
  const tableComponentData = [
    {
      type: "color",
      id: "background-color",
      label: "Table Background Color",
    },
  ];

  const headingComponentData = [
    { type: "fontStyle", id: "font-style", label: "Heading Font Style" },
    { type: "alignment", id: "text-align", label: "Heading Alignment" },
    {
      type: "responsive",
      id: ["font-size", "font-size-tab", "font-size-phone"],
      label: "Heading Font Size",
    },
    {
      type: "responsive",
      id: ["line-height", "line-height-tab", "line-height-phone"],
      label: "Heading Line Height",
    },
    { type: "color", id: "font-color", label: "Heading Font Color" },
    {
      type: "color",
      id: "background-color",
      label: "Heading Background Color",
    },
    { type: "spacing", id: "padding", label: "Heading Padding" },
  ];

  const cellComponentData = [
    { type: "fontStyle", id: "font-style", label: "Cell Font Style" },
    { type: "alignment", id: "text-align", label: "Cell Font Alignment" },
    {
      type: "responsive",
      id: ["font-size", "font-size-tab", "font-size-phone"],
      label: "Cell Font Size",
    },
    {
      type: "responsive",
      id: ["line-height", "line-height-tab", "line-height-phone"],
      label: "Cell Line Height",
    },
    { type: "color", id: "background-color", label: "Cell Background Color" },
    { type: "color", id: "font-color", label: "Cell Font Color" },
  ];

  const cellContainerComponentData = [
    { type: "spacing", id: "padding", label: "Cell Padding" },
  ];

  return (
    <div className="stla-sidebar-settings">
      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="list-field-table"
        settings={tableComponentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />

      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="list-field-heading"
        settings={headingComponentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />

      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="list-field-cell"
        settings={cellComponentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />

      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="list-field-cell-container"
        settings={cellContainerComponentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
    </div>
  );
};
export default ListField;
