import { useState, useEffect } from "@wordpress/element";
import { Spinner } from "@wordpress/components";
import ProPopup from "../../../Helpers/ProPopup";

import { doAction, applyFilters } from "../../../Helpers/createHooks";

const ContentArea = (props) => {
  const [html, setHtml] = useState(false);
  const [parsedHtmlWithEvents, setParsedHtmlWithEvents] = useState("");
  const [loadingApi, setLoadingApi] = useState(false);

  // return "";
  useEffect(() => {
    const controller = new AbortController();
    const signal = controller.signal;

    const fetchData = async () => {
      window.wp.hooks.doAction(
        "stla_booster_on_checkbox_radio_content_load",
        props.checkboxRadioSettings,
        props.formId,
        signal,
        setHtml,
        setLoadingApi,
        props.allSettings
      );
    };

    // Debouncing logic
    const debounceTimer = setTimeout(() => {
      fetchData();
    }, 500); // Adjust the debounce delay as needed

    // Cleanup function to cancel previous request when component unmounts or before next effect
    return () => {
      clearTimeout(debounceTimer);
      controller.abort();
    };
  }, [props.formId, props.checkboxRadioSettings]);

  //attach events when new html is set
  useEffect(() => {
    let contentAreaHtml = "";
    let popupContent = "";

    if (!props.formId) {
      // form Id not set then show form id message
      setParsedHtmlWithEvents(
        <div>Please choose the form in order to apply the styles.</div>
      );
    } else {
      // addon is active.
      if (
        window.stlaAdminGravityBooster.status[props.activeMenuBar] === "active"
      ) {
        if (
          // using the compatible version of addon
          parseInt(
            window.stlaAdminGravityBooster.version[props.activeMenuBar]
          ) >= 2
        ) {
          contentAreaHtml = window.wp.hooks.applyFilters(
            "stla_booster_on_checkbox_radio_html_update",
            contentAreaHtml,
            props.formId,
            html,
            props.setActiveFieldId
          );
        } else {
          // active but old version
          popupContent = {
            title: "Checkbox & Radio addon is outdated",
            description:
              "We're sorry, you are using an old version of Checkbox & Radio addon and it is not compatible with the functionlity. Click the below link to upgrade and use with the latest interface or continue with the old interface.",
            link: "https://wpmonks.com?p=15080",
            secondaryLink: window.stlaAdminGravityBooster.customizerUrl,
            primaryText: "Upgrade Now",
            SecondaryText: "Open Customizer",
            notice:
              "Bonus: Get 70% off on All Addons. Click Here to buy addon bundle at discounted price.",
            noticeLink: "https://wpmonks.com?p=370",
          };

          contentAreaHtml = <ProPopup content={popupContent} />;
        }
      } else if (
        window.stlaAdminGravityBooster.status[props.activeMenuBar] ===
        "inActive"
      ) {
        popupContent = {
          title: "Checkbox & Radio addon is not Active",
          description:
            "We're sorry, your Checkbox & Radio addon is currently inactive. To use it you will first need to activate the addon from your plugin's menu. Click the below link to activate the addon.",
          link:
            window.stlaAdminGravityBooster.adminUrl +
            "plugins.php?plugin_status=inactive",
          primaryText: "Open Plugins",
          notice:
            "Bonus: Get 70% off on All Addons. Click Here to buy addon bundle at discounted price.",
          noticeLink: "https://wpmonks.com?p=370",
        };

        contentAreaHtml = <ProPopup content={popupContent} />;
      } else {
        // addon is not installed
        popupContent = {
          title: "Material is a Pro Addon",
          description:
            "We're sorry, the Checkbox & Radio Styler is premium addon. Click the below link to get the addon.",
          link: "https://wpmonks.com?p=15080",
          primaryText: "Get Checkbox & Radio Styler",
          notice:
            "Bonus: Get 70% off on All Addons. Click Here to buy addon bundle at discounted price.",
          noticeLink: "https://wpmonks.com?p=370",
        };

        contentAreaHtml = <ProPopup content={popupContent} />;
      }
      setParsedHtmlWithEvents(contentAreaHtml);
    }
  }, [html]);

  return (
    <>
      <div className="stla-panel-contentarea stla-responsive-area">
        {window.wp.hooks.applyFilters(
          "stla_booster_checkbox_radio_toolbar",
          props.formId,
          props.totalPageCount,
          props.activePage,
          props.setActivePage
        )}

        <div className="stla-form-wrapper stla-checkbox-radio-form-wrapper">
          {loadingApi && (
            <div className="stla-content-spinner">
              <Spinner />
            </div>
          )}
          {loadingApi === false && parsedHtmlWithEvents}
        </div>
      </div>
    </>
  );
};

export default ContentArea;
