import {
  Button,
  Modal,
  Panel,
  PanelBody,
  PanelRow,
  Icon,
  SearchControl,
  FlexBlock,
  Flex,
} from "@wordpress/components";
import { useState } from "@wordpress/element";
import { more, page, lifesaver } from "@wordpress/icons";
import { cloneDeep, isEmpty } from "lodash";

const HelpPopup = (props) => {
  const panelsData = [
    {
      title: "My Block Settings",
      fields: [
        {
          title: "Creating Your First Form",
          url: "#",
        },
        {
          title: "Creating Your Second Form",
          url: "#",
        },
        {
          title: "Creating Your Third Form",
          url: "#",
        },
        {
          title: "Creating Your Fourth Form",
          url: "#",
        },
        {
          title: "Creating Your Fifth Form",
          url: "#",
        },
      ],
    },
    {
      title: "Your Block Settings",
      fields: [
        {
          title: "Creating Your First Form",
          url: "#",
        },
        {
          title: "Creating Your Second Form",
          url: "#",
        },
        {
          title: "Creating Your Third Form",
          url: "#",
        },
        {
          title: "Creating Your Fourth Form",
          url: "#",
        },
        {
          title: "Creating Your Fifth Form",
          url: "#",
        },
      ],
    },
  ];

  const [searchQuery, setSearchQuery] = useState("");
  const [searchResults, setSearchResults] = useState([]);

  const searchMatchingArticles = (value) => {
    setSearchQuery(value);

    let results = [];

    if (value.length > 3) {
      panelsData.forEach((item) => {
        item.fields.forEach((field) => {
          if (field.title.toLowerCase().includes(searchQuery.toLowerCase())) {
            results.push(field);
          }
        });
      });
    }

    if (isEmpty(results) && value.length > 3) {
      results = [
        {
          title: "No result found",
          url: "#",
        },
      ];
      setSearchResults(results);
    } else {
      setSearchResults(results);
    }
  };

  const panelArticles = (articles) => {
    const articlesStructure = articles.map((article) => {
      return (
        <>
          <li>
            <span className="stla-help-article-icon">
              <Icon icon={page} />
            </span>
            <a href={article.url}>{article.title}</a>
          </li>
        </>
      );
    });

    return <ul className="stla-help-article-list">{articlesStructure}</ul>;
  };

  const accordianStructure = () => {
    const panels = panelsData.map((panel) => {
      return (
        <PanelBody title={panel.title} initialOpen={false}>
          {panelArticles(panel.fields)}
          <Button variant="secondary" href="#" style={{ marginTop: "10px" }}>
            Read our full documentation
          </Button>
        </PanelBody>
      );
    });
    return <Panel>{panels}</Panel>;
  };

  const showSearchResults = () => {
    const allSearchResults = cloneDeep(searchResults);

    const resultsData = allSearchResults.map((article) => {
      return (
        <>
          <li>
            <span className="stla-help-article-icon">
              <Icon icon={page} />
            </span>
            <a href={article.url}>{article.title}</a>
          </li>
        </>
      );
    });

    return (
      <div className="stla-help-search-results">
        <ul className="stla-help-article-list">{resultsData}</ul>
      </div>
    );
  };

  const popupStructure = () => {
    if (props.isHelpPopupOpen === true) {
      return (
        <>
          <Modal
            title=""
            onRequestClose={() => {
              props.setIsHelpPopupOpen(false);
            }}
            overlayClassName="stla-help-popup-overlay"
            isFullScreen={true}
          >
            <div className="stla-help-popoup-container">
              <div className="stla-help-search">
                <SearchControl
                  label={"Search posts"}
                  value={searchQuery}
                  placeholder={"Type your query"}
                  onChange={(value) => {
                    searchMatchingArticles(value);
                  }}
                />
              </div>

              {showSearchResults()}

              <div className="stla-help-folder-strucutre">
                {accordianStructure()}
              </div>

              <div className="stla-help-boxes">
                <Flex gap={5}>
                  <FlexBlock>
                    <div className="stla-help-box">
                      <div className="stla-help-box-icon">
                        <Icon icon={page} />
                      </div>
                      <div className="stla-help-box-content">
                        <h3>View Documentation</h3>
                        <p>
                          Browse documentation, reference material, and
                          tutorials for WPForms.
                        </p>
                        <Button variant="secondary">
                          View All Documentation
                        </Button>
                      </div>
                    </div>
                  </FlexBlock>
                  <FlexBlock>
                    <div className="stla-help-box">
                      <div className="stla-help-box-icon">
                        <Icon icon={lifesaver} />
                      </div>
                      <div className="stla-help-box-content">
                        <h3>Get Support</h3>
                        <p>
                          Browse documentation, reference material, and
                          tutorials for WPForms.
                        </p>
                        <Button variant="primary">
                          View All Documentation
                        </Button>
                      </div>
                    </div>
                  </FlexBlock>
                </Flex>
              </div>
            </div>
          </Modal>
        </>
      );
    }

    return "";
  };

  return <>{popupStructure()}</>;
};

export default HelpPopup;
