export const generalProperties = {
  "reset-styles": -1,
  "force-style": false,
};

export const addCssUnit = (value, unit = "px") => {
  // Regular expression to match CSS units
  const cssUnitRegex = /[a-zA-Z]+$/;

  // If value is already a valid CSS value, return it as is
  if (cssUnitRegex.test(value)) {
    return value;
  } else {
    // If value does not have a CSS unit, add 'px' as default
    return `${value}${unit}`;
  }
};
