import Sidebar from "./Sidebar";
import ContentArea from "./ContentArea";
import { useState } from "@wordpress/element";
const CustomThemes = (props) => {
  const [activeFieldId, setActiveFieldId] = useState(false);

  // console.log(activeFieldId);

  return (
    <div className="stla-panel">
      <Sidebar
        customThemesSettings={props.customThemesSettings}
        setCustomThemesSettings={props.setCustomThemesSettings}
        formId={props.formId}
        activeFieldId={activeFieldId}
        setActiveFieldId={setActiveFieldId}
        stylerSettings={props.stylerSettings}
        setStylerSettings={props.setStylerSettings}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
      <ContentArea
        customThemesSettings={props.customThemesSettings}
        formId={props.formId}
        activeMenuBar={props.activeMenuBar}
        activeFieldId={activeFieldId}
        setActiveFieldId={setActiveFieldId}
        stylerSettings={props.stylerSettings}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
        allSettings={props.allSettings}
      />
    </div>
  );
};

export default CustomThemes;
