import {
  RadioControl,
  FormFileUpload,
  GradientPicker,
  Button,
  Flex,
  FlexItem,
} from "@wordpress/components";
import { cloneDeep } from "lodash";
import Color from "./color";
import { useState } from "@wordpress/element";
import { MediaUpload } from "@wordpress/media-utils";

const Background = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  const updateBackground = (value, key) => {
    let allpanelData = cloneDeep(props.panelData);

    if ("background-type" === key) {
      allpanelData[props.sectionId]["gradient-color"] = null;
      allpanelData[props.sectionId]["background-image"] = "";
      allpanelData[props.sectionId]["background-color"] = "";
    }

    allpanelData[props.sectionId][key] = value;

    props.setHasChangesMade(true);
    props.setPanelData(allpanelData);
  };

  const onSelectMedia = (media) => {
    updateBackground(media.url, "background-image");
    props.setHasChangesMade(true);
  };

  const backgroundStructure = () => {
    const radioControl = (
      <div className="stla-setting-field stla-spacing-control">
        <RadioControl
          label="Background Type"
          onChange={(value) => updateBackground(value, "background-type")}
          selected={sectionSettings["background-type"]}
          className="stla-inline-radio-control"
          options={[
            {
              label: "Color",
              value: "color",
            },
            {
              label: "Image",
              value: "image",
            },
            {
              label: "Gradient",
              value: "gradient",
            },
          ]}
        />
      </div>
    );

    let background,
      image,
      gradientStructure = "";

    if (sectionSettings["background-type"] === "color") {
      background = (
        <div className="stla-setting-field">
          <Color
            panelData={props.panelData}
            setPanelData={props.setPanelData}
            sectionId={props.sectionId}
            settingId="background-color"
            label={"Background Color"}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
          />
        </div>
      );
    }

    if (sectionSettings["background-type"] === "image") {
      image = (
        <div className="stla-setting-field">
          <MediaUpload
            onSelect={(media) => onSelectMedia(media)}
            type="image"
            render={({ open }) => (
              <Button onClick={open} variant="secondary">
                Upload Background Image
              </Button>
            )}
          />
        </div>
      );

      if (sectionSettings["background-image"] !== "") {
        image = (
          <div className="stla-setting-field">
            <label className="stla-sidebar-custom-label">
              Background Image
            </label>
            <div className="stla-uploaded-img">
              <img
                src={sectionSettings["background-image"]}
                style={{ maxWidth: "200px" }}
              />
            </div>

            <div
              className="stla-uploaded-img-actin-btns"
              style={{ marginTop: "10px" }}
            >
              <Button
                variant="primary"
                onClick={() => updateBackground("", ["background-image"])}
                isDestructive={true}
              >
                Remove Image
              </Button>
            </div>
          </div>
        );
      }
    }

    if (sectionSettings["background-type"] === "gradient") {
      gradientStructure = (
        <div className="stla-setting-field">
          <GradientPicker
            value={sectionSettings["gradient-color"]}
            onChange={(currentGradient) =>
              updateBackground(currentGradient, "gradient-color")
            }
          />
        </div>
      );
    }

    return (
      <>
        {radioControl}
        {background}
        {image}
        {gradientStructure}
      </>
    );
  };

  return <>{backgroundStructure()}</>;
};

export default Background;
