import Color from "./components/color";
import Border from "./components/border";
import Spacing from "./components/spacing";
import Unit from "./components/unit";
import Checkbox from "./components/checkbox";
import Alignment from "./components/alignment";
import FontStyle from "./components/fontStyle";
import Responsive from "./components/responsive";
import FontFamily from "./components/fontFamily";
import Background from "./components/background";
// import Responsive from ''

const Components = (props) => {
  const settingsStructure = () => {
    const settingsHtml = props.settings.map((setting, index) => {
      switch (setting.type) {
        case "color":
          return (
            <div className="stla-setting-field">
              <Color
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "checkbox":
          return (
            <div className="stla-setting-field">
              <Checkbox
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "responsive":
          return (
            <div className="stla-setting-field">
              <Responsive
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );
        case "alignment":
          return (
            <div className="stla-setting-field stla-custom-btn-group">
              <Alignment
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "fontStyle":
          return (
            <div className="stla-setting-field stla-custom-btn-group">
              <FontStyle
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "border":
          return (
            <div className="stla-setting-field">
              <Border
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "unit":
          return (
            <div className="stla-setting-field">
              <Unit
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "spacing":
          return (
            <div className="stla-setting-field stla-spacing-control">
              <Spacing
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "fontFamily":
          return (
            <div className="stla-setting-field stla-spacing-control">
              <FontFamily
                panelData={props.panelData}
                setPanelData={props.setPanelData}
                sectionId={props.sectionId}
                settingId={setting.id}
                label={setting.label}
                hasChangesMade={props.hasChangesMade}
                setHasChangesMade={props.setHasChangesMade}
              />
            </div>
          );

        case "background":
          return (
            <Background
              panelData={props.panelData}
              setPanelData={props.setPanelData}
              sectionId={props.sectionId}
              settingId={setting.id}
              label={setting.label}
              
              hasChangesMade={props.hasChangesMade}
              setHasChangesMade={props.setHasChangesMade}
            />
          );
        default:
          return "";
      }
    });

    return settingsHtml;
  };

  return <>{settingsStructure()}</>;
};

export default Components;
