import { isEmpty } from "lodash";
import cssGenerator from "../../../Helpers/cssGenerator";
const StylerInlineStyles = (props) => {
  if (isEmpty(props.stylerSettings)) {
    return "";
  }

  // material design is enabled then don't show styler designs
  if (!isEmpty(props.materialSettings) && !!props.materialSettings.enabled) {
    return "";
  }

  const formWrapperSettings = props.stylerSettings["form-wrapper"];
  const formHeaderSettings = props.stylerSettings["form-header"];
  const formTitle = props.stylerSettings["form-title"];
  const formDescription = props.stylerSettings["form-description"];
  const fieldLabels = props.stylerSettings["field-labels"];
  const subLabels = props.stylerSettings["field-sub-labels"];
  const placeholders = props.stylerSettings["placeholders"];
  const fieldDescriptions = props.stylerSettings["field-descriptions"];
  const textFields = props.stylerSettings["text-fields"];
  const dropdownFields = props.stylerSettings["dropdown-fields"];
  const radioInputs = props.stylerSettings["radio-inputs"];
  const checkboxInputs = props.stylerSettings["checkbox-inputs"];
  const paragraphTextarea = props.stylerSettings["paragraph-textarea"];
  const sectionBreakTitle = props.stylerSettings["section-break-title"];
  const sectionBreakDescription =
    props.stylerSettings["section-break-description"];
  const listFieldTable = props.stylerSettings["list-field-table"];
  const listFieldHeading = props.stylerSettings["list-field-heading"];
  const listFieldCell = props.stylerSettings["list-field-cell"];
  const listFieldCellContainer =
    props.stylerSettings["list-field-cell-container"];
  const submitButton = props.stylerSettings["submit-button"];
  const confirmationMessage = props.stylerSettings["confirmation-message"];
  const errorMessage = props.stylerSettings["error-message"];

  let formDisplay = "block";

  if (props.activePage === "confirmation") {
    formDisplay = "none";
  }
  return (
    <>
      <style>
        {`
        body #gform_wrapper_${props.formId}{
        font-family: ${formWrapperSettings["font"]};
          ${cssGenerator(formWrapperSettings)}
          display: ${formDisplay} !important;
        }

        body #gform_wrapper_${props.formId} .gform_heading{
            ${cssGenerator(formHeaderSettings)}
          }

        body #gform_wrapper_${props.formId} .gform_heading .gform_title {
            ${cssGenerator(formTitle)}
          }

        body #gform_wrapper_${props.formId} .gform_heading .gform_description{
            ${cssGenerator(formDescription)}
          }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_label{
            ${cssGenerator(fieldLabels)}
          }
          
          body #gform_wrapper_${
            props.formId
          } .gform_body .gform_fields .gfield .gfield_label .gfield_required{
            color: ${fieldLabels["asterisk-color"]}
          }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_complex .ginput_full label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_complex .ginput_right label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_complex .ginput_left label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .name_first label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .name_prefix label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .name_middle label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .name_suffix label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .name_last label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .address_line_1 label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .address_line_2 label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .address_city label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .address_state label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .address_zip label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .address_country label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_time_hour label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_time_minute label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_date_month label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_date_day label,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_date_year label{
            ${cssGenerator(subLabels)}
        }

        body #gform_wrapper_${
          props.formId
        } .ginput_container_text input[type="text"]::placeholder{
            ${cssGenerator(placeholders)}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .gfield_description{
            ${cssGenerator(fieldDescriptions)}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield input[type=text],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield input[type=email],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield input[type=tel],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield input[type=url],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield input[type=password],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield input[type=number] {
            ${cssGenerator(textFields)}
        }

        body #gform_wrapper_${
          props.formId
        } div.ginput_complex.ginput_container.ginput_container_name,
        body #gform_wrapper_${props.formId} div.ginput_complex.ginput_container,
        body #gform_wrapper_${
          props.formId
        } .gfield .ginput_container.ginput_container_list {
          width: ${textFields["max-width"]}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields div.ginput_complex.ginput_container.ginput_container_name input[type=text],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields div.ginput_complex.ginput_container.ginput_container_name select,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields div.ginput_complex.ginput_container input[type="text"],
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields div.ginput_complex.ginput_container input select,
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_container.ginput_container_list input[type=text] {
          max-width:100%;
          width:100%
        }

        body #gform_wrapper_${
          props.formId
        }.gform_wrapper .gform_body .gform_fields .gfield_time_ampm select{
          width: calc( 3rem + 20px );
        }

        body #gform_wrapper_${
          props.formId
        } .gform_wrapper .gform_body .gform_fields .gfield_time_hour input,
        body #gform_wrapper_${
          props.formId
        } .gform_wrapper .gform_body .gform_fields .gfield_time_minute input{
          width: calc( 3rem + 8px );
        }


        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield select {
            ${cssGenerator(dropdownFields)}
        }

        body #gform_wrapper_${props.formId} .gfield_radio label{
            ${cssGenerator(radioInputs)}
        }

        body #gform_wrapper_${
          props.formId
        } .gfield .ginput_container_radio .gfield_radio{
           width: ${radioInputs["max-width"]}
        }
        
        body #gform_wrapper_${props.formId} .gfield_checkbox label,
        body #gform_wrapper_${
          props.formId
        } .gfield .ginput_container_consent label{
          width: 100%;
          ${cssGenerator(checkboxInputs)}
        }

        body #gform_wrapper_${props.formId} li .gfield_checkbox label,
        body #gform_wrapper_${
          props.formId
        } li.gfield .ginput_container_consent label{
          width: 100%;
        }

        body #gform_wrapper_${
          props.formId
        } .gfield .ginput_container_checkbox .gfield_checkbox,
        body #gform_wrapper_${props.formId} .gfield .ginput_container_consent {
          width: ${checkboxInputs["max-width"]}
        }
        
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield textarea{
          ${cssGenerator(paragraphTextarea)}

          background: ${paragraphTextarea["background-color"]}
          border-width: ${paragraphTextarea["border-size"]}
          border-color: ${paragraphTextarea["border-color"]}
          border-style: ${paragraphTextarea["border-type"]}
          font-size: ${paragraphTextarea["font-size"]}
          color: ${paragraphTextarea["font-color"]}
          border-radius: ${paragraphTextarea["border-radius"]}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gsection .gsection_title{
          ${cssGenerator(sectionBreakTitle)}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gsection .gsection_description{
          ${cssGenerator(sectionBreakDescription)}
		      padding: 0 16px 0 0 !important;
        }

        body #gform_wrapper_${props.formId} .gform_body .gform_fields .gsection{
          padding-top: ${sectionBreakDescription["padding-top"]};
          padding-right: ${sectionBreakDescription["padding-right"]};
          padding-left: ${sectionBreakDescription["padding-left"]};
          padding-bottom: ${sectionBreakDescription["padding-bottom"]};
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_list{
          ${cssGenerator(listFieldTable)}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_list .gfield_list .gfield_header_item:not(:last-child),
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_list .gfield_list thead th:not(:last-child){
          ${cssGenerator(listFieldHeading)}
        }
        
        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_list .gfield_list .gfield_list_cell input{
          ${cssGenerator(listFieldCell)}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_body .gform_fields .gfield .ginput_list .gfield_list .gfield_list_cell{
          ${cssGenerator(listFieldCellContainer)}
        }
        
        body #gform_wrapper_${props.formId} .gform_footer .gform_button,
        body #gform_wrapper_${props.formId} .gform_page_footer .gform_button,
        body #gform_wrapper_${
          props.formId
        } .gform_page_footer .gform_previous_button,
        body #gform_wrapper_${
          props.formId
        } .gform_page_footer .gform_next_button,
        body #gform_wrapper_${props.formId} .gfield#field_submit .gform-button{
          ${cssGenerator(submitButton)}
        }
        
        body #gform_wrapper_${props.formId} .gform_footer .gform_button:hover,
        body #gform_wrapper_${
          props.formId
        } .gform_page_footer .gform_button:hover,
        body #gform_wrapper_${
          props.formId
        } .gform_page_footer .gform_previous_button:hover,
        body #gform_wrapper_${
          props.formId
        } .gform_page_footer .gform_next_button:hover,
        body #gform_wrapper_${
          props.formId
        } .gfield#field_submit .gform-button:hover{
          background-color: ${submitButton["hover-color"]} !important;
          color: ${submitButton["font-hover-color"]} !important;
        }

        body #gform_wrapper_${props.formId} .gform_footer,
        body #gform_wrapper_${props.formId} .gform_page_footer,
        body #gform_wrapper_${props.formId} #field_submit.gfield{
          text-align: ${submitButton["button-align"]};
          justify-content: ${submitButton["button-align"]};
        }
        
        body #gform_confirmation_message_${props.formId}{
          ${cssGenerator(confirmationMessage)}
        }
        
        body #gform_wrapper_${props.formId} .validation_error,
        body #gform_wrapper_${props.formId} .gform_validation_errors{
          ${cssGenerator(errorMessage)}
        }

        body #gform_wrapper_${
          props.formId
        } .gform_validation_errors h2.gform_submission_error{
          color: ${errorMessage["font-color"]};
          font-size: ${errorMessage["font-size"]};
          line-height: ${errorMessage["line-height"]};
        }

        `}
      </style>
    </>
  );
};

export default StylerInlineStyles;
