import Components from "../../../../Helpers/Components";

const FieldLabels = (props) => {
  const componentData = [
    { type: "checkbox", id: "display", label: "Hide Labels" },
    {
      type: "alignment",
      id: "text-align",
      label: "Font Alignment",
    },
    { type: "fontStyle", id: "font-style", label: "Font Style" },
    {
      type: "responsive",
      id: ["font-size", "font-size-tab", "font-size-phone"],
      label: "Font Size",
    },
    {
      type: "responsive",
      id: ["line-height", "line-height-tab", "line-height-phone"],
      label: "Line Height",
    },
    { type: "color", id: "font-color", label: "Font Color" },
    { type: "color", id: "asterisk-color", label: "Asterisk Color" },
    { type: "spacing", id: "padding", label: "Padding" },
  ];

  return (
    <div className="stla-sidebar-settings">
      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="field-labels"
        settings={componentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
    </div>
  );
};
export default FieldLabels;
