import { Button, ButtonGroup, Dashicon } from "@wordpress/components";
import { cloneDeep } from "lodash";

const FontStyle = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  const updateFontStyle = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    let styleValues = allStylerSettings[props.sectionId][props.settingId];
    let styleArr = styleValues.split("|");

    if (styleArr.length && styleArr.indexOf(value) >= 0) {
      styleArr.splice(styleArr.indexOf(value), 1);
    } else {
      styleArr.push(value);
    }

    styleValues = styleArr.join("|");
    allStylerSettings[props.sectionId][props.settingId] = styleValues;

    props.setHasChangesMade(true);
    props.setPanelData(allStylerSettings);
  };

  const fontAlignmentStructure = () => {
    let variantSecondary = "secondary";
    let variantPrimary = "primary";
    let alignmentValue = sectionSettings[props.settingId];
    return (
      <>
        <label className="stla-sidebar-custom-label">{props.label}</label>
        <div>
          <ButtonGroup>
            <Button
              variant={
                alignmentValue.search("bold") >= 0
                  ? variantPrimary
                  : variantSecondary
              }
              onClick={() => updateFontStyle("bold")}
            >
              <Dashicon icon={"editor-bold"} />
            </Button>
            <Button
              variant={
                alignmentValue.search("italic") >= 0
                  ? variantPrimary
                  : variantSecondary
              }
              onClick={() => updateFontStyle("italic")}
            >
              <Dashicon icon={"editor-italic"} />
            </Button>
            <Button
              variant={
                alignmentValue.search("uppercase") >= 0
                  ? variantPrimary
                  : variantSecondary
              }
              onClick={() => updateFontStyle("uppercase")}
            >
              <Dashicon icon={"editor-spellcheck"} />
            </Button>
            <Button
              variant={
                alignmentValue.search("underline") >= 0
                  ? variantPrimary
                  : variantSecondary
              }
              onClick={() => updateFontStyle("underline")}
            >
              <Dashicon icon={"editor-underline"} />
            </Button>
          </ButtonGroup>
        </div>
      </>
    );
  };

  return <>{fontAlignmentStructure()}</>;
};
export default FontStyle;
