import {
  CheckboxControl,
  TextareaControl,
  SelectControl,
  Button,
  Spinner,
} from "@wordpress/components";
import { useEffect, useState } from "@wordpress/element";
import { cloneDeep } from "lodash";

const GeneralSettings = (props) => {
  const [styledForms, setStyledForms] = useState({});
  const [selectedForm, setSelectedForm] = useState(-1);
  const [deleteApi, setDeleteApi] = useState(false);

  // fetch all the forms which have styles.
  useEffect(() => {
    const fetchFormsWithStyles = async () => {
      try {
        const formData = new FormData();
        formData.append("action", "stla_get_forms_with_styling");
        formData.append("nonce", window.stlaAdminGravityBooster.nonce);
        const response = await fetch(window.ajaxurl, {
          method: "POST",
          body: formData,
        });
        if (!response.ok) {
          throw new Error("Failed to fetch styler settings");
        }

        const result = await response.json();
        if (!!result.success) {
          setStyledForms(result.data);
        }
      } catch (error) {
        console.error("Error fetching styler settings:", error);
      }
    };

    fetchFormsWithStyles();
  }, []);

  const updateGeneralSettings = (value, key) => {
    const modifyGeneralSettings = cloneDeep(props.generalSettings);
    modifyGeneralSettings[key] = value;
    props.setHasChangesMade(true);
    props.setGeneralSettings(modifyGeneralSettings);
  };

  const updateCustomCss = (value) => {
    const modifyStylerSettings = cloneDeep(props.stylerSettings);
    modifyStylerSettings["general-settings"]["custom-css"] = value;
    props.setHasChangesMade(true);
    props.setStylerSettings(modifyStylerSettings);
  };

  const deleteStyles = async () => {
    setDeleteApi(true);
    try {
      const formData = new FormData();
      formData.append("action", "stla_delete_forms_styles");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", selectedForm);

      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to fetch styler settings");
      }

      const result = await response.json();
      if (!!result.success) {
        setStyledForms(result.data);

        await props.fetchStylerSettings();
      }
    } catch (error) {
      console.error("Error fetching styler settings:", error);
    }
    setDeleteApi(false);
  };

  return (
    <div className="stla-sidebar-settings">
      <div className="stla-setting-field">
        <CheckboxControl
          label={
            'Enable This Option If Your Theme Is Overwriting "Styles & Layout Plugin\'s Styling"'
          }
          checked={props.generalSettings["force-style"]}
          onChange={(value) => updateGeneralSettings(value, "force-style")}
        />
      </div>

      <div className="stla-setting-field">
        <TextareaControl
          label="Custom CSS"
          value={props.stylerSettings["general-settings"]["custom-css"]}
          onChange={(value) => updateCustomCss(value)}
        />
      </div>

      <div className="stla-setting-field">
        <SelectControl
          label="Delete Form Styles"
          value={selectedForm}
          options={styledForms}
          onChange={(value) => setSelectedForm(value)}
        />
      </div>

      {selectedForm != -1 && (
        <div className="stla-setting-field">
          <Button variant="primary" onClick={deleteStyles}>
            Delete Styles
            {deleteApi && <Spinner />}
          </Button>
        </div>
      )}
    </div>
  );
};

export default GeneralSettings;
