// import { useState } from "@wordpress/element";
import {
  Flex,
  FlexItem,
  FlexBlock,
  Dashicon,
  DropdownMenu,
  __experimentalTruncate as Truncate,
  Button,
  Icon,
  Spinner,
} from "@wordpress/components";
import { edit } from "@wordpress/icons";
import { useEffect, useState } from "@wordpress/element";
import { isEmpty } from "lodash";

import logo from "../assets/logo.png";

import { cloneDeep } from "lodash";

const Header = (props) => {
  const [allFormsData, setAllFormsData] = useState([]);

  // get all the forms
  useEffect(() => {
    (async function () {
      try {
        const formData = new FormData();
        formData.append("action", "stla_get_all_form_names");
        formData.append("nonce", window.stlaAdminGravityBooster.nonce);
        const response = await fetch(window.ajaxurl, {
          method: "POST",
          body: formData,
        });

        if (!response.ok) {
          throw new Error("Failed to fetch styler settings");
        }

        const result = await response.json();

        if (!!result.success) {
          setAllFormsData(result.data);
        }
      } catch (error) {
        console.error("Error fetching Form Data:", error);
      }
    })();
  }, []);

  const updateFormId = (newFormId) => {
    // Get the current query params
    const queryParams = new URLSearchParams(window.location.search);

    // Check if formId exists in query params
    if (queryParams.has("formId")) {
      // Replace formId with your id
      queryParams.set("formId", newFormId);
    } else {
      // Add formId to query params
      queryParams.append("formId", newFormId);
    }

    // Construct the new URL with modified query params
    const newUrl = window.location.pathname + "?" + queryParams.toString();

    // Reload the page with the new URL
    window.location.href = newUrl;
  };

  let dropdownMenuControls = [];
  if (!isEmpty(allFormsData)) {
    dropdownMenuControls = allFormsData.map((formData) => {
      return {
        title: formData.title,
        onClick: () => updateFormId(formData.id),
      };
    });
  }

  // title of active form.
  let activeTitle = "Select Form";
  allFormsData.forEach((formData) => {
    if (formData.id == props.formId) {
      activeTitle = formData.title;
    }
  });

  const closeBooster = () => {
    // Get the current query params
    const queryParams = new URLSearchParams(window.location.search);

    // Check if formId exists in query params
    if (queryParams.has("page")) {
      // Replace formId with your id
      queryParams.set("page", "gf_edit_forms");
    } else {
      // Add formId to query params
      queryParams.append("page", gf_edit_forms);
    }

    // return to the form editor of current styling form
    if (queryParams.has("formId")) {
      let formId = queryParams.get("formId");
      // Replace formId with your id
      queryParams.set("id", formId);
    }

    // Construct the new URL with modified query params
    const newUrl = window.location.pathname + "?" + queryParams.toString();

    // Reload the page with the new URL
    window.location.href = newUrl;
  };

  let saveBtnDisabeld = cloneDeep(props.savingApi);

  if (!props.hasChangesMade) {
    saveBtnDisabeld = true;
  }

  return (
    <div className="stla-header">
      <Flex justify={"flex-start"}>
        <FlexItem className="stla-header-logo" style={{ width: "90px" }}>
          <img src={logo} />
        </FlexItem>
        <FlexItem>
          <div className="stla-form-select">
            <div className="stla-header-const-text">
              Now editing
              <span className="stla-selected-form">
                <Truncate ellipsizeMode="tail" limit="34">
                  {activeTitle}
                </Truncate>
              </span>
              <DropdownMenu
                className="stla-select-form-dropdown"
                icon={edit}
                label="Change Form"
                controls={dropdownMenuControls}
                popoverProps={{
                  focusOnMount: true,
                  placement: "left-end",
                }}
              />
            </div>
          </div>
        </FlexItem>

        <FlexBlock className="stla-header-middle-block">
          {/* <Button
            variant="tertiary"
            // disabled={props.savingApi}
            // isBusy={props.savingApi}
            className="sage-ai-help-btn"
            onClick={() => props.setIsHelpPopupOpen(true)}
          >
            <Dashicon icon="editor-help" />
            Help
          </Button> */}
        </FlexBlock>

        <FlexItem>
          <Button
            variant="primary"
            onClick={props.saveSettings}
            disabled={saveBtnDisabeld}
            isBusy={props.savingApi}
          >
            {/* {props.savingApi === true && <Spinner />} */}
            <Icon icon="yes" />
            Save
          </Button>
        </FlexItem>

        <FlexItem className="stla-header-exit">
          <Dashicon icon="no-alt" onClick={closeBooster} />
        </FlexItem>
      </Flex>
    </div>
  );
};

export default Header;
